<template>
  <div class="bgfff "style="min-height: 100vh">
    <mt-header  :title="title" style="background: #fff;color: #1B1B1B;">
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
      <!--<mt-button icon="more" slot="right"></mt-button>-->
    </mt-header>


    <div class="list" >
      <div class="item">
        设置本直播间的分成比例
        <input style="width: 100%;border-radius:5px;background: RGBA(238, 238, 238, 1);border: 0;outline:none;line-height: 50px" value="123">
      </div>

    </div>

    <div  class="msg"  style="margin: 25px">
      <div>使用说明</div>
      <div style="color: #F19149;margin: 15px 0">平台方、直播间和被打赏人怎么分配赏金？</div>
      <div>微信官方会扣除2%手续费，剩下的部分由直播间创建者和被打赏人按照上方设置的比例进行分成</div>
      <div  style="color: #F19149;margin: 15px 0">赏金如何提现？</div>
      <div>属于个人的赏金可以自助提现</div>
      <div>属于直播间的赏金，直播间创建者可在直播间设置页面提现到自己钱包</div>
    </div>

    <div style="text-align: center;width: 90%;margin: 0 auto">
      <el-button type="info" style="width: 100%;">关闭赞赏</el-button>
    </div>

  </div>

</template>

<script>
  export default {
    data () {
      return {
        title:"赞赏设置"
      }
    },
    methods:{
    },
    mounted(){

    }
  }
</script>


<style lang="less" scoped>
  .msg div{
    font-family: PingFang SC;
    line-height: 25px;
  }
  .list{
    background: #fff;
    padding: 0 15px;
  .item{
    border-bottom: 1px solid #f2f2f2;
    line-height: 40px;
  }
  }

</style>



